import React from "react";
import logo from "./logo.svg";
import "./App.css";
import { AnswerVm, QuizDto, QuizStorrage } from "./quizStorrage";

function App() {
  const quiz = QuizStorrage.test;
  const answers = quiz.groups
    .flatMap((g) => g.questions)
    .sort((q) => q.number)
    .map<AnswerVm>((q) => ({
      question: q,
    }));

  return (
    <div className="App">
      {answers.map((answer) => (
        <div
          className="d-flex flex-column
       justify-content-center align-items-center"
        >
          <h3>
            {answer.question.number}. {answer.question.value}
          </h3>

          <div className="btn-group" role="group">
            {[1, 2, 3, 4, 5, 6].map((ans_val) => [
              <input
                type="radio"
                className="btn-check"
                autoComplete="off"
                name={`btnradio_${answer.question.number}_${ans_val}`}
                id={`btnradio_${answer.question.number}_${ans_val}`}
                onClick={() => (answer.value = ans_val)}
              ></input>,
              <label
                className="btn btn-outline-primary"
                htmlFor={`btnradio_${answer.question.number}_${ans_val}`}
              >
                {ans_val}
              </label>,
            ])}
          </div>
        </div>
      ))}
    </div>
  );
}

export default App;
